  letter-spacing: 0.1em;
  padding: 1vw 0;
  border: 1px solid #251e1c;
  transition: all 0.6s;
  position: relative;
}
.btn-more:after {
  content: "";
  width: 2.8461538462vw;
  height: 1px;
  background: #251e1c;
  position: absolute;
  right: -2.1538461538vw;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.6s;
}
.btn-more:hover {
  color: #fff;
  background: #251e1c;
}
.btn-more:hover:after {
  right: -1.3846153846vw;
}
.btn-more.en {
  font-size: 1vw;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  line-height: 1em;
  letter-spacing: 0.06em;
}
.btn-more.color02 {
