  }
}

.scroll-anim.to-anim-top {
  transition: 0.8s all ease;
  transition-delay: 0.4s;
  opacity: 0;
  transform: translateY(10px);
}
.scroll-anim.to-anim-top.active {
  transform: translateY(0);
